    font-family: Roboto Flex, sans-serif;
}

body {
    margin: 0;
}

.container {
    height: 778px;
    display: grid;
    grid-template-columns: 1fr 4fr;
}

.sidebar {
    padding-left: 20px;
    background-color: #B3ADEB;
    color: white;
    display: grid;
    grid-template-rows: 2fr 1fr 6fr;
    grid-template-areas: 
    "header"
    "add-project-button"
    "project-cards";
}

.header-container {
    grid-area: header;
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-areas: 
    "check-img header-text";
    align-items: center;
}

.assignment-header-img {
    grid-area: check-img;
}

.assignment-header-img img {
    width: 50px;
}

.assignment-header {
    grid-area: header-text;
}

.header {
    color: #d6e9fa;
    font-size: 30px;
    font-weight: 800;
}

.add-project-button-container {
    grid-area: add-project-button;
    display: flex;
    justify-content: center;
}

.add-project-button {
    height: 40px;
    background-color: #988FE0;
    flex: 1 1 auto;
    max-width: 200px;
    min-width: 140px;
    margin-right: 20px;
    border: 1px solid #D1CEF3;
    border-radius: 4px;
    font-size: 16px;
}

.add-project-button:hover {
    background-color: #8A7FDC;
    transition: 0.3s;
}

.project-card-container {
    grid-area: project-cards;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 0 10px;
}

.project-card {
    max-width: 200px;
    min-width: 140px;
}

.top-buttons-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 6px;
    padding: 6px;
    border-radius: 4px;
}

.top-buttons-container:hover {
    background-color: #7DB2DE;
    transition: 0.3s;
}

.top-buttons-container img {
    width: 20px;
    cursor: pointer;
}

.project-card-title { 
    all: unset; /* Reset all default button styles */
    flex-grow: 4;
    text-align: center;
    cursor: pointer;
    color: inherit; /* Match text color with surrounding text */
    font: inherit; /* Match font with surrounding text */
    border-radius: 4px;
    font-size: 18px;
    font-weight: 600;
}

.remove-project-button {
    height: 20px;
    border: none;
    border-radius: 4px;
    background-color: #fdb79d;
}

.remove-project-button:hover {
    background-color: #ff7846;
    transition: 0.3s;
}

.bottom-buttons-container {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    padding-left: 15px;
}

.project-todo-button,
.project-note-button {
    height: 20px;
    flex-grow: 1;
    border: none;
    border-radius: 4px;
    background-color: #fcf1d8;
}

.project-todo-button:hover,
.project-note-button:hover {
    background-color: #fce2a5;
    transition: 0.3s;
}

.main-content-container {
    background-color: #5da1d8;
    display: grid;
    grid-template-rows: 1fr 4fr;
}

.new-todo-note-button-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.new-todo-button,
.new-note-button {
    height: 40px;
    flex: 1 1 auto;
    max-width: 180px;
    min-width: 140px;
    margin-right: 20px;
    border: 1px solid #4486f7;
    border-radius: 4px;
    font-size: 16px;
    background-color: #c2d5f7;
}

.new-todo-button:hover,
.new-note-button:hover {
    background-color: #9dbff8;
    transition: 0.3s;
}

.todo-note-card-container {
    display: flex;
    padding: 10px 60px;
}

.todo-card-container,
.note-card-container {
    background-color: #AECFEA;
    flex: 1 1 auto;
    border: 1px solid #2A6CA2;
    border-radius: 4px;
}

.todo-card,
.todo-card-title-container,
.todo-card-description-container,
.todo-card-due-date-container,
.todo-card-priority-container,
.note-card,
.note-card-title-container,
.note-card-description-container {
    margin: 5px;
    border: 1px solid black;
    border-radius: 4px;
    display: flex;
}

.todo-card-title-container,
.todo-card-description-container,
.todo-card-due-date-container,
.todo-card-priority-container,
.note-card-title-container,
.note-card-description-container {
    background-color: #DEE7F7;
}

.todo-card,
.note-card {
    background-color: aliceblue;
    display: grid;
    align-items: center;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 1fr 1.5fr;
}

.todo-card {
    grid-template-areas: 
    "todo-title todo-edit-remove"
    "todo-description due-date-priority";
}

.note-card {
    grid-template-areas: 
    "note-title note-edit-remove"
    "note-description note-description";
}

.todo-card-title-container,
.note-card-title-container {
    height: 35px;
    align-items: center;
}

.todo-card-title-container {
    grid-area: todo-title;
}

.note-card-title-container {
    grid-area: note-title;
}

.todo-card-title,
.note-card-title {
    padding-left: 10px;
}

.edit-remove-todo-container,
.edit-remove-note-container {
    height: 30px;
    padding: 0 10px;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.edit-remove-todo-container {
    grid-area: todo-edit-remove;
}

.edit-remove-note-container {
    grid-area: note-edit-remove;
}

.edit-todo-button,
.remove-todo-button,
.edit-note-button,
.remove-note-button {
    flex: 1 1 auto;
    max-width: 100px;
    min-width: 50px;
    border: 1px solid;
    border-radius: 4px;
}

.edit-todo-button,
.edit-note-button {
    background-color: #fbec8d;
}

.edit-todo-button:hover,
.edit-note-button:hover {
    background-color: #f2dd54;
}

.remove-todo-button,
.remove-note-button {
    background-color: #fdb79d;
}

.remove-todo-button:hover,
.remove-note-button:hover {
    background-color: #ff7846;
    transition: 0.3s;
}

.todo-card-description-container,
.note-card-description-container {
    height: 75px;
    padding-left: 10px;
}

.todo-card-description-container {
    grid-area: todo-description;
}

.note-card-description-container {
    grid-area: note-description;
}

.todo-card-description,
.note-card-description {
    margin-top: 7px;
}

.todo-due-date-priority-container {
    grid-area: due-date-priority;
    display: flex;
    flex-direction: column;
}

.todo-card-due-date-container,
.todo-card-priority-container {
    height: 30px;
    min-width: 85px;
    justify-content: center;
    align-items: center;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,oCAAoC;AACxC;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,aAAa;IACb,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,kBAAkB;IAClB,yBAAyB;IACzB,YAAY;IACZ,aAAa;IACb,+BAA+B;IAC/B;;;mBAGe;AACnB;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,8BAA8B;IAC9B;2BACuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,6BAA6B;IAC7B,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,YAAY;IACZ,yBAAyB;IACzB,cAAc;IACd,gBAAgB;IAChB,gBAAgB;IAChB,kBAAkB;IAClB,yBAAyB;IACzB,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,wBAAwB;IACxB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,UAAU,EAAE,oCAAoC;IAChD,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,cAAc,EAAE,2CAA2C;IAC3D,aAAa,EAAE,qCAAqC;IACpD,kBAAkB;IAClB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,SAAS;IACT,kBAAkB;AACtB;;AAEA;;IAEI,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;;IAEI,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;;IAEI,YAAY;IACZ,cAAc;IACd,gBAAgB;IAChB,gBAAgB;IAChB,kBAAkB;IAClB,yBAAyB;IACzB,kBAAkB;IAClB,eAAe;IACf,yBAAyB;AAC7B;;AAEA;;IAEI,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;;IAEI,yBAAyB;IACzB,cAAc;IACd,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;;;;;;;;IAQI,WAAW;IACX,uBAAuB;IACvB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;;;;;;IAMI,yBAAyB;AAC7B;;AAEA;;IAEI,2BAA2B;IAC3B,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,6BAA6B;AACjC;;AAEA;IACI;;wCAEoC;AACxC;;AAEA;IACI;;uCAEmC;AACvC;;AAEA;;IAEI,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;;IAEI,kBAAkB;AACtB;;AAEA;;IAEI,YAAY;IACZ,