import { todoCardDisplay, clearTodoCardDisplay, todoCardContainer } from "./todos.js";
import { noteCardDisplay, clearNoteCardDisplay, noteCardContainer } from "./notes.js";
import { myProjects, populateStorage, editRemoveFromStorage } from "./storage.js";

// global variables
let projectItemPath;

// querySelectors
const projectCardContainer = document.querySelector('.project-card-container');
const mainContentContainer = document.querySelector('.main-content-container');
const todoNoteButtonContainer = document.createElement('div');
const todoNoteCardContainer = document.createElement('div');
mainContentContainer.appendChild(todoNoteButtonContainer).className = 'new-todo-note-button-container';
mainContentContainer.appendChild(todoNoteCardContainer).className = 'todo-note-card-container';

// creates project constructor
class Project {
    constructor(title, todo, note) {
        this.title = title;
        this.todo = todo;
        this.note = note;
    }
}

// creates new instance of project object
const makeProject = (projectTitle) => {
    let title = projectTitle;
    let myTodos = [title];
    let myNotes = [title];
    let newProject = new Project(title, myTodos, myNotes);
    addProjectToArray(newProject);
    makeProjectCard(newProject);
}
  
// pushes project to array
const addProjectToArray = (newProject) => {
    myProjects.push(newProject);
    populateStorage();
}

// displays instance of new project
const makeProjectCard = () => {
    projectCardContainer.replaceChildren();

    for (const project of myProjects) { // iterates through content of each project card
        const newProjectCard = document.createElement('div');
        const topButtonsContainer = document.createElement('div');
        const titleButtonImage = document.createElement('img');
        const projectTitleButton = document.createElement('button');
        const removeProjectButton = document.createElement('button');
        const bottomButtonsContainer = document.createElement('div');
        const projectTodoButton = document.createElement('button');
        const projectNoteButton = document.createElement('button');
        
        projectTitleButton.textContent = `${project.title}`;
        projectTodoButton.textContent = "Todos";
        projectNoteButton.textContent = "Notes";
        removeProjectButton.textContent = "X";

        titleButtonImage.src = require('./images/check-box-20.svg');
        titleButtonImage.alt = 'small check mark';
        titleButtonImage.setAttribute('data-pin-nopin', 'true'); // removes Pinterest browser extension feature
        topButtonsContainer.appendChild(titleButtonImage);
        topButtonsContainer.appendChild(projectTitleButton).className = 'project-card-title project-card-buttons';
        topButtonsContainer.appendChild(removeProjectButton).className = 'remove-project-button project-card-buttons';
        newProjectCard.appendChild(topButtonsContainer).className = 'top-buttons-container';
        newProjectCard.appendChild(bottomButtonsContainer).className = 'bottom-buttons-container';
        projectCardContainer.appendChild(newProjectCard).className = 'project-card';
    
        toggleTodoNoteButtons(titleButtonImage, projectTitleButton, bottomButtonsContainer, projectTodoButton, projectNoteButton);

        removeProjectListener(removeProjectButton, newProjectCard, topButtonsContainer);

        todoButtonListener(projectTodoButton, topButtonsContainer);

        noteButtonListener(projectNoteButton, topButtonsContainer);
    }
}

// toggles between showing and hiding project's Todos & Notes buttons
const toggleTodoNoteButtons = (titleButtonImage, projectTitleButton, bottomButtonsContainer, projectTodoButton, projectNoteButton) => {
    [titleButtonImage, projectTitleButton].forEach(element => {
        element.addEventListener('click', () => {
            if (projectTodoButton.parentNode === bottomButtonsContainer) {
                bottomButtonsContainer.removeChild(projectTodoButton);
                bottomButtonsContainer.removeChild(projectNoteButton);
            } else if (projectTodoButton.parentNode === null) {
                bottomButtonsContainer.appendChild(projectTodoButton).className = 'project-todo-button project-card-buttons';
                bottomButtonsContainer.appendChild(projectNoteButton).className = 'project-note-button project-card-buttons';
            }
        });
    });
}

// gives eventListener functionality to removeProjectButton
const removeProjectListener = (removeProjectButton, newProjectCard, topButtonsContainer) => {
    removeProjectButton.addEventListener('click', () => {
        projectItemPath = topButtonsContainer.childNodes[1].firstChild.textContent;
        removeProjectFromArray(projectItemPath);
        removeProjectFromDom(newProjectCard);
        removeTodoNoteButton();
        clearTodoCardDisplay();
        clearNoteCardDisplay();
    });
}

// gives eventListener functionality to 'Todo' button
const todoButtonListener = (projectTodoButton, topButtonsContainer) => {
    projectTodoButton.addEventListener('click', () => {
        projectItemPath = topButtonsContainer.childNodes[1].firstChild.textContent;
        removeTodoNoteButton();
        removeTodoNoteCardContainer();
        if (todoNoteCardContainer === null) {
            mainContentContainer.appendChild(todoNoteCardContainer).className = 'todo-note-card-container';
        }
        todoNoteCardContainer.appendChild(todoCardContainer).className = 'todo-card-container';
        todoCardDisplay();
    });
}

// gives eventListener functionality to 'Note' button
const noteButtonListener = (projectNoteButton, topButtonsContainer) => {
    projectNoteButton.addEventListener('click', () => {
        projectItemPath = topButtonsContainer.childNodes[1].firstChild.textContent;
        removeTodoNoteButton();
        removeTodoNoteCardContainer();
        if (todoNoteCardContainer === null) {
            mainContentContainer.appendChild(todoNoteCardContainer).className = 'todo-note-card-container';
        }
        todoNoteCardContainer.appendChild(noteCardContainer).className = 'note-card-container';
        noteCardDisplay();
    });
}

// removes user specified project from myProjects array
const removeProjectFromArray = (projectItemPath) => {
    const targetObject = myProjects.find(obj => obj.title === projectItemPath);
    let index = myProjects.indexOf(targetObject);
    if (index !== -1) {
        myProjects.splice(index, 1);
    }
    editRemoveFromStorage();
}

// removes user specified project from DOM
const removeProjectFromDom = (newProjectCard) => {
    projectCardContainer.removeChild(newProjectCard);
}

// removes 'New Todo/Note' button from display
const removeTodoNoteButton = () => {
    if (todoNoteButtonContainer.firstChild !== null) {
        let buttonToRemove = todoNoteButtonContainer.firstChild;
        todoNoteButtonContainer.removeChild(buttonToRemove);
    }
}

// clears todoNoteCardContainer from DOM when switching between todos and notes
const removeTodoNoteCardContainer = () => {
    if (todoNoteCardContainer !== null) {
        todoNoteCardContainer.replaceChildren()
    }
}

export { makeProject, makeProjectCard, myProjects, projectItemPath, todoNoteButtonContainer, todoNoteCardContainer };