import { myProjects, projectItemPath, todoNoteButtonContainer, todoNoteCardContainer } from "./projects";
import { populateStorage, editRemoveFromStorage } from "./storage";

// global variables
let targetTodoArray;
let targetTodoObject;
let todoItemPath;
let newTodoButton;
let todoCardContainer;
let editButtonClicked = false;
let editedTodoObject;

// querySelectors
const todoDialog = document.querySelector("#todo-dialog");
const todoFormContainer = document.querySelector(".todo-form-container");
const todoHeader = document.querySelector(".todo-title");
const submitTodoButton = document.querySelector(".button-todo-submit");
const todoTitle = document.querySelector("#todo-title");
const todoDescription = document.querySelector("#todo-description");
const todoDueDate = document.querySelector("#due-date");
const todoPriority = document.querySelector("#priority");
todoCardContainer = document.createElement('div');

// creates todo constructor
class Todo {
    constructor(title, description, dueDate, priority) {
        this.title = title;
        this.description = description;
        this.dueDate = dueDate;
        this.priority = priority;
    }
}

// creates todo's display
const todoCardDisplay = () => {
    if (newTodoButton === undefined) { // checks if 'new todo' button has been created in user selected project ie. creates initial button
        createNewTodoButton();
    }

    if  (targetTodoArray !== undefined) { // checks if todo array of selected project has data, if so creates 'new todo' button
        createNewTodoButton();
    }

    if (todoCardContainer === undefined) {
        todoNoteCardContainer.appendChild(todoCardContainer).className = 'todo-card-container';
    }

    projectDestination(projectItemPath);
    refreshTodoCardDisplay();
    makeTodoListener();
}

// creates 'new todo' button
const createNewTodoButton = () => {
    newTodoButton = document.createElement('button');
    newTodoButton.textContent = "New Todo";
    todoNoteButtonContainer.appendChild(newTodoButton).className = 'new-todo-button';
}

// gives eventListener functionality to new todo button
const makeTodoListener = () => {
    newTodoButton.addEventListener('click', () => {
        editButtonClicked = false;
        todoHeader.textContent = "Add new todo";
        todoInputForm();
    })
}

// pops out todo form
const todoInputForm = () => {
    todoFormContainer.reset(); // resets previously entered value
    todoDialog.showModal(); // shows form container
}

// gives eventListener functionality to 'Submit' button
submitTodoButton.addEventListener('click', () => {
    makeNewTodo(projectItemPath, todoTitle, todoDescription, todoDueDate, todoPriority);
});

// closes form dialog and form container when clicking outside form container
todoDialog.addEventListener('click', () => todoDialog.close());

// prevents form container from closing when clicking inside form container itself
todoFormContainer.addEventListener('click', (event) => event.stopPropagation());

// creates new todo object
const makeNewTodo = (projectItemPath, todoTitle, todoDescription, todoDueDate, todoPriority) => {
    projectDestination(projectItemPath);
    let title = todoTitle.value;
    let description = todoDescription.value;
    let dueDate = todoDueDate.value;
    let priority = todoPriority.value;
    let newTodo = new Todo (title, description, dueDate, priority);

    if (editButtonClicked === true) {
        editedTodoObject = newTodo;
        editTodoInArray();
        refreshTodoCardDisplay();
        editButtonClicked = false;
        return;
    }

    addTodoToProject(newTodo);

    if (todoCardContainer.parentElement !== null) {
        clearTodoCardDisplay();
    }

    targetTodoArray.slice(1).forEach(todo => {
        makeTodoCard(todo.title, todo.description, todo.dueDate, todo.priority);
    });
}

// finds requested project destination
const projectDestination = (projectItemPath) => {
    let targetProjectObject = myProjects.find(obj => obj.title == projectItemPath);
    targetTodoArray = targetProjectObject.todo;
}

// finds requested note destination
const todoDestination = () => {
    targetTodoObject = targetTodoArray.find(obj => obj.title === todoItemPath);
}

// pushes todo into array of specified project
const addTodoToProject = (newTodo) => {
    targetTodoArray.push(newTodo);
    populateStorage();
}

// displays instance of new todo
const makeTodoCard = (title, description, dueDate, priority) => {
    let newTodoCard = document.createElement('div');
    const todoCardTitleContainer = document.createElement('div');
    const todoCardTitle = document.createElement('h2');
    const todoCardDescriptionContainer = document.createElement('div');
    const todoCardDescription = document.createElement('p');
    const dueDatePriorityContainer = document.createElement('div');
    const todoCardDueDateContainer = document.createElement('div');
    const todoCardDueDate = document.createElement('p');
    const todoCardPriorityContainer = document.createElement('div');
    const todoCardPriority = document.createElement('p');
    const editRemoveTodoContainer = document.createElement('div');
    const editTodoButton = document.createElement('button');
    const removeTodoButton = document.createElement('button');

    todoCardTitle.textContent = `${title}`;
    todoCardDescription.textContent = `${description}`;
    todoCardDueDate.textContent = `${dueDate}`;
    todoCardPriority.textContent = `${priority}`;
    editTodoButton.textContent = "Edit";
    removeTodoButton.textContent = "X";

    editTodoListener(editTodoButton);

    removeTodoListener(removeTodoButton, newTodoCard);

    todoCardTitleContainer.appendChild(todoCardTitle).className = 'todo-card-title';
    todoCardDescriptionContainer.appendChild(todoCardDescription).className = 'todo-card-description';
    todoCardDueDateContainer.appendChild(todoCardDueDate).className = 'todo-card-due-date';
    todoCardPriorityContainer.appendChild(todoCardPriority).className = 'todo-card-priority';
    dueDatePriorityContainer.appendChild(todoCardDueDateContainer).className = 'todo-card-due-date-container';
    dueDatePriorityContainer.appendChild(todoCardPriorityContainer).className = 'todo-card-priority-container';
    editRemoveTodoContainer.appendChild(editTodoButton).className = 'edit-todo-button todo-card-buttons';
    editRemoveTodoContainer.appendChild(removeTodoButton).className = 'remove-todo-button todo-card-buttons';
    newTodoCard.appendChild(todoCardTitleContainer).className = 'todo-card-title-container';
    newTodoCard.appendChild(editRemoveTodoContainer).className = 'edit-remove-todo-container';
    newTodoCard.appendChild(todoCardDescriptionContainer).className = 'todo-card-description-container';
    newTodoCard.appendChild(dueDatePriorityContainer).className = 'todo-due-date-priority-container';
    todoCardContainer.appendChild(newTodoCard).className = 'todo-card';
}

// gives eventListener functionality to edit todo button
const editTodoListener = (editTodoButton) => {
    editTodoButton.addEventListener('click', () => {
        editButtonClicked = true;
        todoItemPath = editTodoButton.parentElement.parentElement.childNodes[0].textContent;
        todoHeader.textContent = "Edit todo";
        projectDestination(projectItemPath);
        todoDestination();
        todoInputForm();
    });
}

// edits user specified todo object in targetTodoArray
const editTodoInArray = () => {
    let index = targetTodoArray.indexOf(targetTodoObject);
    if (index !== -1) {
        targetTodoArray[index] = editedTodoObject;
    }
    editRemoveFromStorage();
}

// gives eventListener functionality to remove todo button
const removeTodoListener = (removeTodoButton, newTodoCard) => {
    removeTodoButton.addEventListener('click', () => {
        todoItemPath = removeTodoButton.parentElement.parentElement.childNodes[0].textContent;
        removeTodoFromArray();
        removeTodoFromDom(newTodoCard);
    });
}

// removes user specified todo object from targetTodoArray
const removeTodoFromArray = (removeTodoButton) => {
    projectDestination(projectItemPath);
    todoDestination(removeTodoButton); 
    let index = targetTodoArray.indexOf(targetTodoObject);
    if (index !== -1) {
        targetTodoArray.splice(index, 1);
    }
    editRemoveFromStorage();
}

// removes user specified todo from DOM
const removeTodoFromDom = (newTodoCard) => {
    todoCardContainer.removeChild(newTodoCard);
}

// refreshes todo card display
const refreshTodoCardDisplay = () => {
    clearTodoCardDisplay();
    if (targetTodoArray !== undefined) {
        targetTodoArray.slice(1).forEach(todo => {
            makeTodoCard(todo.title, todo.description, todo.dueDate, todo.priority);
        });
    }
}

// clears todo card display
const clearTodoCardDisplay = () => {
    if (todoCardContainer !== undefined) {
        todoCardContainer.innerHTML = "";
    }
}
export { todoCardDisplay, clearTodoCardDisplay, todoCardContainer };