if (process.env.NODE_ENV !== "production") {
  console.log("Looks like we are in development mode!");
}

import "./style.css";
import { makeProject } from "./projects.js";
import { storageCheck } from "./storage.js";

// querySelectors
const addProjectButton = document.querySelector('.add-project-button');
const projectDialog = document.querySelector("#project-dialog");
const projectFormContainer = document.querySelector(".project-form-container");
const submitButton = document.querySelector(".button-submit");
const projectTitle = document.querySelector("#project-title");

storageCheck();

addProjectButton.addEventListener('click', () => {
  projectInputForm();
});

// pops out project form
const projectInputForm = () => {
  projectFormContainer.reset(); // resets previously entered value
  projectDialog.showModal(); // shows form container
}

submitButton.addEventListener('click', () => {
  if (projectTitle.value === '') {
    alert("Please name your project");
  } else {
    makeProject(projectTitle.value);
  }
});

// closes form dialog and form container when clicking outside form container
projectDialog.addEventListener('click', () => projectDialog.close());

// prevents form container from closing when clicking inside form container itself
projectFormContainer.addEventListener('click', (event) => event.stopPropagation());