import { makeProjectCard } from "./projects";

// test for localStorage availability
function storageAvailable(type) {
    let storage;
    try {
      storage = window[type];
      const x = "__storage_test__";
      storage.setItem(x, x);
      storage.removeItem(x);
      return true;
    } catch (e) {
      return (
        e instanceof DOMException &&
        e.name === "QuotaExceededError" &&
        // acknowledge QuotaExceededError only if there's something already stored
        storage &&
        storage.length !== 0
      );
    }
  }

//   executes above test function
if (storageAvailable("localStorage")) {
    console.log("Yippee! We can use localStorage awesomeness");
} else {
    console.log("Too bad, no localStorage for us");
} 


// allows myProject to be defined as a getter & setter
const obj = {};

Object.defineProperties(obj, {
  myArray: {
    get() {
      return this._array || [];
    },
    set(newArray) {
      if (Array.isArray(newArray)) {
        this._array = newArray;
      } else {
        throw new TypeError('Value must be an array');
      }
    },
    enumerable: true,
    configurable: true
  }
});

// global variables
let myProjects = obj.myArray; // assigns myProjects to have getter & setter attributes
let currentProjectsArray;

// querySelectors
const projectTitleForm = document.getElementById('project-title');
const todoTitleForm = document.getElementById('todo-title');
const todoDescriptionForm = document.getElementById('todo-description');
const todoDueDateForm = document.getElementById('due-date');
const todoPriorityForm = document.getElementById('priority');
const noteTitleForm = document.getElementById('note-title');
const noteDescriptionForm = document.getElementById('note-description');

// checks for stored data in localStorage
const storageCheck = () => {
    if (!JSON.parse(localStorage.getItem('my-projects'))) { // executes if there is no stored data
        populateStorage();
    } else {
        setDisplay();
    }
}

// populates/saves data into localStorage
function populateStorage() {    
    localStorage.setItem('project-title', document.getElementById('project-title').value);
    localStorage.setItem('todo-title', document.getElementById('todo-title').value);
    localStorage.setItem('todo-description', document.getElementById('todo-description').value);
    localStorage.setItem('due-due', document.getElementById('due-date').value);
    localStorage.setItem('priority', document.getElementById('priority').value);
    localStorage.setItem('note-title', document.getElementById('note-title').value);
    localStorage.setItem('note-description', document.getElementById('note-description').value);

    localStorage.setItem('my-projects', JSON.stringify(myProjects));
}

// retrieves data from localStorage
function setDisplay() {
    currentProjectsArray = JSON.parse(localStorage.getItem('my-projects'));
    let currentProjectTitle = localStorage.getItem('project-title');
    let currentTodoTitle = localStorage.getItem('todo-title');
    let currentTodoDescription = localStorage.getItem('todo-description');
    let currentDueDate = localStorage.getItem('due-due');
    let currentPriority = localStorage.getItem('priority');
    let currentNoteTitle = localStorage.getItem('note-title');
    let currentNoteDescription = localStorage.getItem('note-description');
    
    if (myProjects.length === 0) {
        myProjects = currentProjectsArray;
    }
    
    makeProjectCard();

    document.getElementById('project-title').value = currentProjectTitle;
    document.getElementById('todo-title').value = currentTodoTitle;
    document.getElementById('todo-description').value = currentTodoDescription;
    document.getElementById('due-date').value = currentDueDate;
    document.getElementById('priority').value = currentPriority;
    document.getElementById('note-title').value = currentNoteTitle;
    document.getElementById('note-description').value = currentNoteDescription;
}

// allows edit & removes changes to be recorded in localStorage
function editRemoveFromStorage() {
    currentProjectsArray = myProjects;
    populateStorage();
}

// listens to element changes and save them
projectTitleForm.onchange = populateStorage;
todoTitleForm.onchange = populateStorage;
todoDescriptionForm.onchange = populateStorage;
todoDueDateForm.onchange = populateStorage;
todoPriorityForm.onchange = populateStorage;
noteTitleForm.onchange = populateStorage;
noteDescriptionForm.onchange = populateStorage;

export { populateStorage, setDisplay, storageCheck, editRemoveFromStorage };
export { myProjects };