import { myProjects, projectItemPath, todoNoteButtonContainer, todoNoteCardContainer } from "./projects";
import { populateStorage, editRemoveFromStorage } from "./storage";

// globalizes target objects
let targetNoteArray;
let targetNoteObject;
let noteItemPath;
let newNoteButton;
let noteCardContainer;
let editButtonClicked = false;
let editedNoteObject;

// querySelectors
const noteDialog = document.querySelector("#note-dialog");
const noteFormContainer = document.querySelector(".note-form-container");
const noteHeader = document.querySelector('.note-title');
const submitNoteButton = document.querySelector(".button-note-submit");
const noteTitle = document.querySelector("#note-title");
const noteDescription = document.querySelector("#note-description");
noteCardContainer = document.createElement('div');

// creates note constructor
class Note {
    constructor(title, description) {
        this.title = title;
        this.description = description;
    }
}

// creates note's display
const noteCardDisplay = () => {
    if (newNoteButton === undefined) { // checks if 'new note' button has been created in user selected project ie. creates initial button
        createNewNoteButton();
    } 

    if  (targetNoteArray !== undefined) { // checks if note array of selected project has data, if so creates 'new note' button
        createNewNoteButton();
    }
    
    if (noteCardContainer === undefined) {
        todoNoteCardContainer.appendChild(noteCardContainer).className = 'note-card-container';
    }

    projectDestination(projectItemPath);
    refreshNoteCardDisplay();
    makeNoteListener();
}

// creates 'new note' button
const createNewNoteButton = () => {
    newNoteButton = document.createElement('button');
    newNoteButton.textContent = "New Note";
    todoNoteButtonContainer.appendChild(newNoteButton).className = 'new-note-button';
}

// gives eventListener functionality to new note button
const makeNoteListener = () => {
    newNoteButton.addEventListener('click', () => {
        editButtonClicked = false;
        noteHeader.textContent = "Add new note";
        noteInputForm();
    })
}

// pops out note form
const noteInputForm = () => {
    noteFormContainer.reset(); // resets previously entered value
    noteDialog.showModal(); // shows form container
}

// gives eventListener functionality to 'Submit' button
submitNoteButton.addEventListener('click', () => {
    makeNewNote(projectItemPath, noteTitle, noteDescription);
});

// closes form dialog and form container when clicking outside form container
noteDialog.addEventListener('click', () => noteDialog.close());

// prevents form container from closing when clicking inside form container itself
noteFormContainer.addEventListener('click', (event) => event.stopPropagation());

// creates new note object
const makeNewNote = (projectItemPath, noteTitle, noteDescription) => {
    projectDestination(projectItemPath);
    let title = noteTitle.value;
    let description = noteDescription.value;
    let newNote = new Note (title, description);

    if (editButtonClicked === true) {
        editedNoteObject = newNote;
        editNoteInArray();
        refreshNoteCardDisplay();
        editButtonClicked = false;
        return;
    }

    addNoteToProject(newNote);

    if (noteCardContainer.parentElement !== null) {
        clearNoteCardDisplay();
    }

    targetNoteArray.slice(1).forEach(note => {
        makeNoteCard(note.title, note.description);
    });
}

// finds requested project destination
const projectDestination = (projectItemPath) => {
    let targetProjectObject = myProjects.find(obj => obj.title == projectItemPath);
    targetNoteArray = targetProjectObject.note;
}

// finds requested note destination
const noteDestination = () => {
    targetNoteObject = targetNoteArray.find(obj => obj.title === noteItemPath);
}

// pushes note into array of specified project
const addNoteToProject = (newNote) => {
    targetNoteArray.push(newNote);
    populateStorage();
}

// displays instance of new note
const makeNoteCard = (title, description) => {
    let newNoteCard = document.createElement('div');
    const noteCardTitleContainer = document.createElement('div');
    const noteCardTitle = document.createElement('h2');
    const noteCardDescriptionContainer = document.createElement('div');
    const noteCardDescription = document.createElement('p');
    const editRemoveNoteContainer = document.createElement('div');
    const editNoteButton = document.createElement('button');
    const removeNoteButton = document.createElement('button');

    noteCardTitle.textContent = `${title}`;
    noteCardDescription.textContent = `${description}`;
    editNoteButton.textContent = "Edit";
    removeNoteButton.textContent = "X";

    editNoteListener(editNoteButton);

    removeNoteListener(removeNoteButton, newNoteCard);

    noteCardTitleContainer.appendChild(noteCardTitle).className = 'note-card-title';
    noteCardDescriptionContainer.appendChild(noteCardDescription).className = 'note-card-description';
    editRemoveNoteContainer.appendChild(editNoteButton).className = 'edit-note-button note-card-buttons';
    editRemoveNoteContainer.appendChild(removeNoteButton).className = 'remove-note-button note-card-buttons';
    newNoteCard.appendChild(noteCardTitleContainer).className = 'note-card-title-container';
    newNoteCard.appendChild(editRemoveNoteContainer).className = 'edit-remove-note-container';
    newNoteCard.appendChild(noteCardDescriptionContainer).className = 'note-card-description-container';
    noteCardContainer.appendChild(newNoteCard).className = 'note-card';
}

// gives eventListener functionality to edit note button
const editNoteListener = (editNoteButton) => {
    editNoteButton.addEventListener('click', () => {
        editButtonClicked = true;
        noteItemPath = editNoteButton.parentElement.parentElement.childNodes[0].textContent;
        noteHeader.textContent = "Edit note";
        projectDestination(projectItemPath);
        noteDestination();
        noteInputForm();
    });
}

// edits user specified note object in targetNoteArray
const editNoteInArray = () => {
    let index = targetNoteArray.indexOf(targetNoteObject);
    if (index !== -1) {
        targetNoteArray[index] = editedNoteObject;
    }
    editRemoveFromStorage();
}

// gives eventListener functionality to remove note button
const removeNoteListener = (removeNoteButton, newNoteCard) => {
    removeNoteButton.addEventListener('click', () => {
        noteItemPath = removeNoteButton.parentElement.parentElement.childNodes[0].textContent;
        removeNoteFromArray();
        removeNoteFromDom(newNoteCard);
    });
}

// removes user specified note object from targetNoteArray
const removeNoteFromArray = (removeNoteButton) => {
    projectDestination(projectItemPath);
    noteDestination(removeNoteButton); 
    let index = targetNoteArray.indexOf(targetNoteObject);
    if (index !== -1) {
        targetNoteArray.splice(index, 1);
    }
    editRemoveFromStorage();
}

// removes user specified note from DOM
const removeNoteFromDom = (newNoteCard) => {
    noteCardContainer.removeChild(newNoteCard);
}

// refreshes note card display
const refreshNoteCardDisplay = () => {
    clearNoteCardDisplay();
    if (targetNoteArray !== undefined) {
        targetNoteArray.slice(1).forEach(note => {
            makeNoteCard(note.title, note.description);
        });
    }
}

// clears note card display
const clearNoteCardDisplay = () => {
    if (noteCardContainer !== undefined) {
        noteCardContainer.innerHTML = "";
    }
}

export { noteCardDisplay, clearNoteCardDisplay, noteCardContainer };